import React from "react";
import "./style.css"
import NyanCat from "./Entities/NyanCat"

import SolarSystem from "./SolarSystem"

export default class Space extends React.Component{

    frameData = {
        last: +new Date(),
        count: 0,
        total: 0,
        average: 0,
        fails: 0,
    }

    constructor(props){
        super(props);

        this.state = {
            cats: [<NyanCat key={"0"} />]
        }
    }

    componentDidMount () {
        const onRequestAnimationFrame = () => {
            if(this.calcFPS())
                this.afRequest = window.requestAnimationFrame(onRequestAnimationFrame)
            else console.log(`Final at ${this.state.cats.length}`)
        }
        this.afRequest = window.requestAnimationFrame(onRequestAnimationFrame)
    }
    
    componentWillUnmount () {
        window.cancelAnimationFrame(this.afRequest)
    }

    calcFPS(){
        const data = this.frameData;
        const now = +new Date();
        data.count++;
        data.delta = now - data.last;
        data.total += data.delta;
        data.average = data.total / data.count;

        data.last = now;

        if(data.count % 300 === 0)
        {   
            if(data.delta < data.average && data.average < 18)
                this.addCat()
            else data.fails++;
        }

        return data.fails < 5;
    }

    addCat(){
        const cats = this.state.cats;
        cats.push(<NyanCat key={`${cats.length}`} />)
        this.setState(cats)
    }

    render(){
        return (
            <div className="container">
                <SolarSystem/>
                {this.props.children}
                {this.state.cats.map(m => m)}
            </div>
        )
    }
}